body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    text-align: center;
    margin-top: 50px;
}

html {
    scroll-behavior: smooth;
}

body {
    background-color: #db382b;
    color: white;
}

a {
    color: white;
}

#content {
    padding: 0 20px;
    position: relative;
    margin: 0px auto 0px auto;
}

.figures {
    width: 80%;
    max-width: 1000px;
    background-color: #ffffff;
    border-radius: 50px;
    margin: 40px auto;
}

#protected_areas_figures__title {
    text-align: center;
    font-size: 30px;
    color: black;
}

#protected_areas_figures {
    width: 90%;
    padding: 0 0 10px 0;
    margin: 10px auto 10px auto;
    display: inline-block;
}

.graphTD {
    padding:0;
}

.graphTR{
    width : 100%;
}

.graphTable{
    width : 90%;
}
.figureMap__card {
    width: 90%;
    padding: 0 0 10px 0;
    margin: 10px auto 10px auto;
    display: inline-block;
}

.figureMap__card_title {
    color: black;
    text-align: center;
}

#svgmap {
    width: 100%;
    height: 100%;
    border-radius: 30px;
}

.figureMap__card_legend {
    width: 100%;
    height: 20px;
    color: black;
    padding-bottom: 20px;
}

text{
    font-size: 12px;
    color: black;
}


.figureMap__card_info {
    height: 20px;
    font-size: 18px;
    color: black;
    padding-bottom: 20px;
}

.figureMap__card .map path {
    fill: #aaa;
    stroke: #fff;
}




.figureDistrib__card {
    width: 100%;
    margin: 75px auto 10px auto;
}


.figureDistrib__card_info {
    height: 20px;
    font-size: 18px;
    color: black;
}

.figureDistrib__card_title {
    color: black;
    text-align: center;
}

.tick{
    color: black;
    font-weight: bold;
}
.figureTerrestrial {
    width: 100%;    
}

.figureTerrestrial__title {
    color: black;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
}

.figureTerrestrial__card {
    width: 100%;
    margin: 10px 0px 10px 0px;
}

.legendTerrestrial {
    font-size: 16px;
}

.axis-label {
    font-size: 14px;
}

.x-axis line {
    stroke: gray;
}

.y-axis line {
    stroke: gray;
}

.x-axis path {
    stroke: gray;
}

.y-axis path {
    stroke: gray;
}
.figureMarine {
    width: 100%;
}

.figureMarine__title {
    color: black;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
}

.figureMarine__card {
    width: 100%;
    margin: 10px 0px 10px 0px;
}

.legendMarine {
    font-size: 16px;
}

.axis-label {
    font-size: 14px;
}

.x-axis line {
    stroke: gray;
}

.y-axis line {
    stroke: gray;
}

.x-axis path {
    stroke: gray;
}

.y-axis path {
    stroke: gray;
}
.figureTree__card {
    width: 90%;
    margin: auto;
    display: inline-block;
}

#imgTree {
    width: 100%;
    border-radius: 30px;
    margin: auto;
}

.node {
    cursor: pointer;
}

.node circle {
  fill: #fff;
  stroke: #aaa;
  stroke-width: 3px;
}

.node text {
  font: 12px sans-serif;
}

.link {
  fill: none;
  stroke: rgba(139, 0, 0, 0.328);
  stroke-width: 1px;
}

#header {
    align-items: center;
    margin: 100px auto 50px auto;
    text-align: center;
    font-size: large;
}
.navbar__logo img {
    flex: 1 1;
    height: 50px;
}

.navbar {
    overflow: hidden;
    top: 0;
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px 0 20px;
    background-color: #b1271d;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
    z-index: 1;
}

.navbar a:hover {
  color: black;
}
.navbar__navbar-links {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex: 1 1;
}

a {
    text-decoration: none;
    font-size: 1.5rem;
    margin: 0 10px;
    transition: all 0.3s ease-in;
}

table {
    margin: 0px auto 0px auto;
}

th, td {
    padding: 15px;
}
